import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select, Switch } from "antd";
import Swal from "sweetalert2";

import {
  isRejectModelVisible,
  rejectModelInputChangeValue,
  salesApproval,
  getRejectReasonData
} from "../../../../store/sideMenu/vehicleCounterSales/vehicleCounterSalesSlice";
import { getMiniRejectReasons } from "../../../../store/mini/miniSlice";
import { func } from "prop-types";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const SalesRejectModel = (props) => {
  useEffect(() => {
    props.getMiniRejectReasons({ type: [2, 3] });
  }, []);
  function ChangeHandeler(e) {
    props.rejectModelInputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  function rejectReasonById(id){
    props.miniRejectReasonsList.forEach((rejectReason) => {
      if (rejectReason.id == id) {
        props.getRejectReasonData(rejectReason);
        props.rejectModelInputChangeValue({
          key: "editable",
          value: rejectReason.editable,
        });
      }
    });
  }

  // Adds New Country
  const handleAdd = () => {
    var approveObj = {
      sale_id: props.vehicleSaleData.id,
      levelapproved: props.vehicleSaleData.approvedlevel + 1,
      approvalstatus: 3,
      reason_id: props.salesRejectData.reason_id,
      description: props.salesRejectData.description,
      editable: props.salesRejectData.editable,
    };
    if (approveObj.reason_id !== null && approveObj.description !== null) {
      Swal.fire({
        title: "Are you sure ?",
        text: "Reject this Sale!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, Reject it!",
      }).then((result) => {
        if (result.isConfirmed) {
          props.salesApproval(approveObj);
        }
      });
    }
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isRejectModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleAdd();
          }}
          type="primary"
          block
        >
          Submit
        </Button>
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = <h5 className="modal-title">Sale Reject</h5>;



  return (
    <Modal
      visible={props.RejectMode}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isRejectModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical">
        <Form.Item label="Reason :">
          <Select
            showSearch
            placeholder="Select a Reason"
            optionFilterProp="Reason"
            filterOption={(input, option) =>
              option.children
                .toLowerCase()
                .includes(input.toLowerCase())
            }
            onChange={(value) => {
              rejectReasonById(value);
              props.rejectModelInputChangeValue({
                key: "reason_id",
                value: value,
              });
            }}
            value={
              props.salesRejectData.reason_id == null
                ? ""
                : props.salesRejectData?.reason_id
            }
          >
            {props.miniRejectReasonsList.map((rejectreasons, index) => {
              return (
                <Option key={index} value={rejectreasons.id}>
                  {rejectreasons.name}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        {props.rejectReasonObjData.editable && (
          <Form.Item
          label="Editable"
          name="editable"
          rules={[
            {
              required: true,
              message: "Please Select Editable",
            },
          ]}
          hasFeedback
        >
          {" "}
          <Switch
            checkedChildren="On"
            unCheckedChildren="Off"
            // defaultChecked finalapproval
            checked={props.rejectReasonObjData.editable}
            onChange={(value) => {
              props.rejectModelInputChangeValue({
                key: "editable",
                value: value,
              });
            }}
          />
        </Form.Item>
        )}
        <Form.Item label="Description:">
          <Input.TextArea
            id="description"
            name="description"
            style={{ width: "100%" }}
            placeholder="description"
            // value={props.rowdata?.description}
            value={props.salesRejectData?.description}
            onChange={ChangeHandeler}
            rules={[
              { required: true, message: "Please input your Description!" },
            ]}
          />
        </Form.Item>

        
      </Form>
    </Modal>
  );
};

function mapStateToProps({ counterSales, mini }) {
  return {
    vehicleSaleData: counterSales.vehicleSaleData,
    RejectMode: counterSales.RejectMode,
    salesRejectData: counterSales.salesRejectData,
    miniRejectReasonsList: mini.miniRejectReasonsList,
    rejectReasonObjData: counterSales.rejectReasonObjData
  };
}

export default connect(mapStateToProps, {
  isRejectModelVisible,
  rejectModelInputChangeValue,
  getMiniRejectReasons,
  salesApproval,
  getRejectReasonData
})(SalesRejectModel);
