import { Fragment, useState } from "react";
import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  Modal,
  Row,
  Col,
  List,
  Popconfirm,
  Form,
  Input,
  Table,
  Spin,
} from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import moment from "moment";
import { isFvViewModelVisible } from "../../../../store/sideMenu/vehicleCounterSales/vehicleCounterSalesSlice";

const FvModelView = (props) => {
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  const [form] = Form.useForm();
  const { item } = props;
  const handleResetInputValues = () => {
    form.resetFields();
    props.isFvViewModelVisible(false);
  };

  const FVDataView = [
    {
      title: "CODE",
      description:
        props.fVRequestModelData?.code == null
          ? "none"
          : props.fVRequestModelData.code,
    },
    {
      title: "COUNTER",
      description:
        props.fVRequestModelData?.counter == null
          ? "none"
          : props.fVRequestModelData.counter?.name,
    },
    {
      title: "REQUESTED BY",
      description:
        props.fVRequestModelData?.requestedby == null
          ? "none"
          : props.fVRequestModelData.requestedby?.fullname,
    },
    {
      title: "REQUESTED ON",
      description:
        props.fVRequestModelData?.requestedon == null
          ? "none"
          : moment(props.fVRequestModelData.requestedon).format(
              "DD-MM-YYYY, h:mm:ss a"
            ),
    },
    {
      title: "REQUESTED TO",
      description:
        props.fVRequestModelData?.requestedto == null
          ? "none"
          : props.fVRequestModelData.requestedto?.fullname,
    },
    {
      title: "SUBMITTED BY",
      description:
        props.fVRequestModelData?.submittedby == null
          ? "none"
          : props.fVRequestModelData.submittedby?.fullname,
    },
    {
      title: "SUBMITTED ON",
      description:
        props.fVRequestModelData?.submittedon == null
          ? "none"
          : moment(props.fVRequestModelData.submittedon).format(
              "DD-MM-YYYY, h:mm:ss a"
            ),
    },
    {
      title: "VERIFICATION STATUS",
      description:
        props.fVRequestModelData?.verficationstatus_name == null
          ? "none"
          : props.fVRequestModelData.verficationstatus_name,
    },
  ];

  var FVDetails = [];
  FVDataView.forEach((data) => {
    if (data.title !== null) {
      FVDetails.push(data);
    }
  });

  // var enquiry = [];
  // enquiryList.forEach((item) => {
  //   if (item.title !== null) {
  //     enquiry.push(item);
  //   }
  // });

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "2%",
    },
    {
      title: "Questions",
      render: (row) => <span className={columnClass}>{row.question}</span>,
      width: "15%",
    },
    {
      title: "Answers",
      render: (row) => <span className={columnClass}>{row.answer}</span>,
      width: "15%",
    },
  ];

  const data = props.fVRequestModelData.fieldverficationanswer?.map(
    (row, key) => ({
      ...row,
      index: key + 1,
    })
  );

  const FooterAddOrUpdate = () => {
    return <Fragment></Fragment>;
  };

  const FooterCancel = () => {
    return <Fragment></Fragment>;
  };

  const modalTitle = <h5 className="modal-title">Field Verification View</h5>;

  return (
    <Modal
      visible={props.viewModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isFvViewModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={<></>}
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={816}
    >
      <Row gutter={[12, 12]}>
        <Col span={24} md={24}>
          <Spin tip="Loading..." spinning={props.loading} delay={500}>
            <List
              grid={{
                gutter: 16,
                xs: 1,
                sm: 2,
                md: 3,
                lg: 3,
                xl: 4,
                xxl: 3,
              }}
              dataSource={FVDetails}
              renderItem={(fvItem) => (
                <List.Item>
                  <List.Item.Meta
                    title={<span>{fvItem.title}</span>}
                    description={<span>{fvItem.description}</span>}
                  />
                </List.Item>
              )}
            />
          </Spin>
        </Col>
        <Col span={24} md={24}>
          <Table
            columns={columns}
            bordered
            rowClassName="hp-btn-bg-color-none"
            dataSource={data}
            pagination={false}
            size="middle"
          />
        </Col>
      </Row>
    </Modal>
  );
};

function mapStateToProps({ counterSales }) {
  return {
    fVRequestModelData: counterSales.fVRequestModelData,
    viewModel: counterSales.viewModel,
    loading: counterSales.loading,
  };
}

export default connect(mapStateToProps, {
  isFvViewModelVisible,
})(FvModelView);
