import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  Input,
  Form,
  Modal,
  Badge,
  Row,
  Col,
  Select,
  Popconfirm,
} from "antd";
import { RiErrorWarningLine } from "react-icons/ri";

import {
  isRequestModelVisible,
  requestModelInputChangeValue,
  FieldVerificationRequest,
} from "../../../../store/sideMenu/vehicleCounterSales/vehicleCounterSalesSlice";
// import { getMiniRejectReasons } from "../../../../store/mini/miniSlice";
import {
  getFVRequestUsers,
  setFVRequestUsersParams,
} from "../../../../store/mini/miniSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const SalesRequestModel = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getFVRequestUsers({ page: 1, search: "", page_size: 10 });
  }, []);
  function ChangeHandeler(e) {
    props.requestModelInputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    var approveObj = {
      sale_id: props.vehicleSaleData.id,
      counter_id: props.vehicleSaleData.counter.id,
      requestedto_id: props.salesRequestData.requestedto,
      verficationstatus: 1,

      // levelapproved: props.vehicleSaleData.approvedlevel + 1,
      // approvalstatus: 3,
      // reason_id: props.salesRejectData.reason_id,
      // description: props.salesRejectData.description,
    };
    props.FieldVerificationRequest(approveObj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isRequestModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Popconfirm
          placement="topRight"
          title="Are you sure?"
          onConfirm={() => form.submit()}
          okText="Yes"
          cancelText="No"
          className="hp-ml-8"
          icon={
            <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
          }
        >
          {" "}
          <Button type="primary" block>
            Submit
          </Button>
        </Popconfirm>
        ;
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = <h5 className="modal-title">Field Verfication Request</h5>;
  useEffect(() => {
    form.setFieldsValue({
      user_id: props.salesRequestData.user_id,
    });
  }, [props.salesRequestData]);
  return (
    <Modal
      visible={props.RequestMode}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isRequestModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form
        form={form}
        layout="vertical"
        autoComplete="off"
        onFinish={() => {
          handleAdd();
        }}
      >
        <Col span={24} xs={24} xl={24} md={24}>
          <Form.Item
            label="User"
            name="requestedto"
            rules={[
              {
                required: true,
                message: "Please Select User",
              },
            ]}
            hasFeedback
          >
            <Select
              showSearch
              placeholder="Select User"
              optionFilterProp="User"
              // disabled
              // filterOption={(input, option) =>
              //   option.children.toLowerCase().includes(input.toLowerCase())
              // }
              onSearch={(value) => {
                props.setFVRequestUsersParams({
                  ...props.FVRequestUsersParams,
                  page: 1,
                  search: value,
                });
                props.getFVRequestUsers({
                  ...props.FVRequestUsersParams,
                  page: 1,
                  search: value,
                });
              }}
              onChange={(value) => {
                props.requestModelInputChangeValue({
                  key: "requestedto",
                  value: value,
                });
              }}
              onPopupScroll={() => {
                if (
                  props.FVRequestUsersParams.page <
                  props.FVRequestUsersParams.no_of_pages
                ) {
                  props.getFVRequestUsers({
                    ...props.FVRequestUsersParams,
                    page: props.FVRequestUsersParams.page + 1,
                  });
                  props.setFVRequestUsersParams({
                    ...props.FVRequestUsersParams,
                    page: props.FVRequestUsersParams.page + 1,
                  });
                }
              }}
              value={
                props.salesRequestData.user_id == null
                  ? ""
                  : props.salesRequestData?.user_id
              }
            >
              {props.FVRequestUsersList.map((fvuser, index) => {
                return (
                  <Option key={index} value={fvuser.id}>
                    {fvuser.username + "-" + fvuser.fullname}
                  </Option>
                );
              })}
            </Select>
          </Form.Item>
        </Col>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ counterSales, mini }) {
  return {
    vehicleSaleData: counterSales.vehicleSaleData,
    RequestMode: counterSales.RequestMode,
    salesRequestData: counterSales.salesRequestData,
    // miniRejectReasonsList: mini.miniRejectReasonsList,
    FVRequestUsersList: mini.FVRequestUsersList,
    FVRequestUsersParams: mini.FVRequestUsersParams,
  };
}

export default connect(mapStateToProps, {
  isRequestModelVisible,
  requestModelInputChangeValue,
  // getMiniRejectReasons,
  FieldVerificationRequest,
  getFVRequestUsers,
  setFVRequestUsersParams,
})(SalesRequestModel);
